body{background-color:#282d34;color:#fff;padding:0;box-sizing:border-box;margin:0;font-family:"Open Sans";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-y:scroll}a{color:#fff;font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;transition:-webkit-text-decoration ease 0.2s;transition:text-decoration ease 0.2s;transition:text-decoration ease 0.2s, -webkit-text-decoration ease 0.2s;-webkit-text-decoration:underline solid;text-decoration:underline solid;-webkit-text-decoration-color:rgba(255,255,255,0.7);text-decoration-color:rgba(255,255,255,0.7);text-decoration-thickness:2px}a:hover{-webkit-text-decoration-color:white;text-decoration-color:white}.link-button{display:flex;justify-content:space-around;align-items:center;width:auto;margin:1rem;padding:0.5rem;border-radius:20px;border:1px solid #1f2428;background-color:#1f2428;transition:0.2s;text-decoration:none}.link-button>*{margin:0.25rem}.link-button:hover{background-color:#282d34;border:5px solid #1f2428}.page-container{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;font-size:1.25rem;min-height:80vh;padding-bottom:6rem}.page-container>*{width:calc(min(80vw, 45rem))}.info-box{border-radius:2rem;background-color:#1f2428;padding:1.5rem;transition:ease 0.5s}.info-box:hover{transform:scale(1.03);transform-origin:50%, 50%}.center{text-align:center}h1,h2,h3{font-family:Montserrat;margin-left:0rem;margin-top:0.5rem;margin-bottom:0.5rem}hr{width:auto;height:0;border:0.2rem solid #1f2428;border-radius:15px;margin-bottom:1.5rem}.info-box>hr{border-color:#282d34}ul{padding-left:1.5rem}ul>li{padding-bottom:0.25rem}img{display:block;margin-left:auto;margin-right:auto}img.profile{width:calc(max(40%, 20rem));border-radius:2rem;transition:ease-out 0.4s;box-shadow:0px 0px 20px 5px #1f2428}img.profile:hover{transform:scale(1.025);transform-origin:50%, 50%}svg{height:1em}.languages{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;font-style:italic}.fade-in{opacity:0;transform:translateY(1rem);-webkit-animation:fadeIn 0.7s ease-in-out 0.2s forwards;animation:fadeIn 0.7s ease-in-out 0.2s forwards}@-webkit-keyframes fadeIn{from{opacity:0;transform:translateY(1rem)}to{opacity:1;transform:translateY(0)}}@keyframes fadeIn{from{opacity:0;transform:translateY(1rem)}to{opacity:1;transform:translateY(0)}}

.navbar{width:100%;height:calc(min(15vw, 4rem));background-color:#1f2428;display:flex;justify-content:space-between}.nav-elements{display:flex;height:100%;flex-direction:row;justify-content:space-between;align-items:center}a.nav-link{display:block;height:100%;width:auto;padding-left:2rem;padding-right:2rem;text-decoration:none;font-size:1.5rem;transition:ease 0.05s}a.nav-link:hover{background-color:#282d34}a.nav-link:hover>.nav-element{transform:translate(0, -0.2rem)}.nav-element{display:flex;justify-content:center;align-items:center;height:100%;margin:0;transition:ease-out 0.4s}.home-button{font-style:italic}

#root{position:relative;min-height:100vh}#root>#footer{position:absolute;right:0;bottom:0;left:0;height:auto;text-align:center}#root>#footer>div{display:flex;justify-content:center;align-items:center;padding:0.5rem}.link-button{font-size:1.25rem}

.card-list{display:grid;justify-items:center;align-items:center;justify-content:center;width:calc(min(850px, 90vw));grid-gap:2rem;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr))}.project-card{background-color:#1f2428;width:calc(min(400px, 90%));height:30rem;border-radius:2rem;overflow:hidden;transition:all ease 0.5s}.project-card>img{object-fit:cover;object-position:50% 50%;width:100%;height:10rem;border-radius:1rem;box-shadow:0px 0px 30px 0px #282d34;margin-bottom:1rem;transition:ease 0.8s;z-index:-1}.project-card:hover{transform:scale(1.05)}.project-card:hover img{transform:scale(1.05)}.project-card h1,.project-card h2,.project-card h3{text-align:center}.project-card p{margin:1rem}.project-card hr{border-color:#282d34;background-color:#282d34}

